// importy z pliów zewnętrznych
@import "introFirstMenu";

//reset
*{ margin: 0; padding: 0; }

html, body { width: 100%; height: 100%; }

//grid
.row:before, .row:after{
    content: "";
    display: table;
    clear: both;
}

[class*='col-']{
  float: left;
  min-height: 1px;
  width: 100%/12;
  padding: 12px;
}

@for $i from 1 through 12 { .col-#{$i} { width: (100%/12)*$i; } }

//wysokość nagłówka i stopki
header, footer{ height: 5vh; }

//wysokość nawigacji, części głównej i alertowej
nav, #mainPart, #alerts{ height: 78vh; }

//style dla głównego kontenera zawierającego header, nav, mainPart, alert i footer
.grid-display{
  width: 1300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}


//style dla nawigacji i nagłówka
.flexForBtns{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: flex-start;
}

//podstawowy styl dla przycisku
.basicBtn{
  width: 40%;
  height: 7%;
  font-weight: bold;
  font-size: 1.05em;
  text-shadow: 1px 1px white;
}

//tekst średniowieczny
.medievalText{
  font-family: 'MedievalSharp', cursive;
}

//podstawowy styl dla tekstu paragrafu
.basicText{
  text-indent: 3%;
  font-weight: bold;
  margin-top: 2%;
  font-size: 1.3em;
  text-shadow: 1px 1px white;
}

//wyśrodkowanie tekstu
.center{
	text-align: center;
}

//szerokość 24 procent
.width24{
	width: 24%;
}

//szerokość 33 procent
.width33{
	width: 33%;
}

//szerokość 49 procent
.width49{
	width: 49%;
}

//szerokość 100%
.width100{
	width: 100%;
}

//niebieski kolor tekstu
.blueText{
  color: blue;
}

//zielony kolor tekstu
.greenText{
	color: green;
}
